.Button {
    display: inline-block;
    text-align: center;
    text-transform: uppercase;
    padding: 1.19rem 1.37rem;
    min-width: 170px;
    font-size: 15px;
    border: 1px solid #D9D9D9;
    background: #fff;
    width: 50% !important;
    
}
.Button a{
    text-decoration: none;
    color: #000;
    letter-spacing: .1em;
    transition: background .2s ease,border .2s ease;
    margin-top: 1rem;
    font-weight: 700;
    
    
}



